/* src/styles/contact.css */

.contact-container {
    max-width: 600px;
    margin: 0 auto;
    color: #fff;
  }
  
  .contact-container h2 {
    font-size: 2rem;
    margin-bottom: 20px;
  }
  
  .contact-form {
    display: flex;
    flex-direction: column;
  }
  
  .contact-form label {
    margin-top: 10px;
    margin-bottom: 5px;
  }
  
  .contact-form input,
  .contact-form textarea {
    padding: 10px;
    border: none;
    border-radius: 5px;
  }
  
  .contact-form button {
    margin-top: 15px;
    padding: 10px;
    background-color: #ff6f6f;
    color: #fff;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    transition: background-color 0.3s ease;
  }
  
  .contact-form button:hover {
    background-color: #ff4c4c;
  }
  